body {
  margin: 0;
  padding: 0;
  color: #222222;
  font-weight: 500;
  font-family: "Gilroy", "Arial", sans-serif;
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 340px;
  margin: 0 auto;
}
@media (min-width: 700px) {
  .container {
    width: 700px;
  }
}
@media (min-width: 1180px) {
  .container {
    width: 1180px;
  }
}
@media (min-width: 1660px) {
  .container {
    width: 1660px;
  }
}

