  padding: 0;
  font-size: 0;
  line-height: 0;
  list-style: none;
  position: relative;
  display: flex;
  align-items: center;
}

.user-menu__item {
  display: block;
  margin-right: 30px;
}
.user-menu__item--active svg {
  color: #f9b300;
}
@media (min-width: 700px) {
  .user-menu__item {
    margin-right: 60px;
  }
}
@media (min-width: 1180px) {
  .user-menu__item {
    margin-right: 40px;
  }
  .user-menu__item:hover svg {
    color: #e2a300;
  }
}
