  .user-menu__item {
    margin-right: 54px;
  }
}
.user-menu__item:last-child {
  margin-right: 0;
}

.user-menu__icon {
  width: auto;
  height: 17px;
  color: #222222;
  vertical-align: middle;
}
.user-menu__icon--login {
  height: 20px;
}
